<template tabindex="-1"
    @keyup.enter="handleSubmit">
<section class="section main-section">
    <div class="container box">



      <h1 class="title">Login</h1>
        
        <form>
        <b-field label="Username" class="has-text-left">
            <b-input placeholder="username" rounded
            size="is-medium"
            icon="account"
            v-model="username"
            :disabled="loading"
            v-on:keyup.native.enter="handleSubmit()"
            ></b-input>
        </b-field>
        <b-field label="Password" class="has-text-left">
            <b-input placeholder="password" rounded
            size="is-medium"
            icon="form-textbox-password"
            type="password"
            v-model="password"
            :disabled="loading"
            v-on:keyup.native.enter="handleSubmit()"
            ></b-input>
        </b-field>
     <div class="has-text-centered">
          <b-button
                type="is-link"  
                size="is-medium"
                icon-left="login-variant"
                :loading="loading"
                @click="handleSubmit()"
                >
                Login
            </b-button>
      </div>
       </form> 

        <b-notification style="margin-top:3rem" v-if="error"
            type="is-warning"
            has-icon
            aria-close-label="Close notification"
            role="alert"
            :active.sync="notIsActive"
            class="not-box">
            <b class="not-txt">{{errorMsg}}</b>
        </b-notification>
    

    </div>

 <p style="margin:0.6rem" ><small v-if="isPasswordAdmin">Login info is now <b>admin:admin</b>, you should change the password after login</small></p>
 
 <p style="margin:0.6rem"  ><small>In case you forgot the password run the command <b>pyWebCamMonitorCtl reset-admin-password</b></small></p>


  </section>
</template>


<script>

 
export default {
  name: 'LoginPage',
  data () {
    return {
      username:'',
      password:'',
      isPasswordAdmin:false,
      error:false,
    loading:false,
    errorMsg:'',
    notIsActive: false,
    }
  },
  props: {
    msg: String
  },
  created(){
      this.$axios.get(this.$axios.defaults.apiEndpoints['user-is-password-admin']).then(
         resp  => {
          if(resp.data.isPassAdmin){
        this.isPasswordAdmin = true
      }
        }
      );
      
  },
  methods:{
handleSubmit() {
this.loading = true
this.notIsActive = true

            //User name and password are added to axis's auth after simple verification
            
            if( this.username.length === 0 || this.password .length === 0 ){
               this.error = true
               this.errorMsg = "Username and password are required!"
               this.loading = false
               return;
            }
            
            this.$axios.defaults.auth = {
                username: this.username,
                password: this.password,
            }
            
        this.$axios.post('/api/login', {
    withCredentials: true,
  timeout:7000 ,
    headers: {
      "Accept": "application/json",
      "Content-Type": "application/json"
    }
  } ).then(response => {
                let data = response.data
                //Save token
                this.$store.commit('set_token', data)
                this.loading = false
                this.$router.push('/')
            // eslint-disable-next-line no-unused-vars
            }).catch(error => {
                 this.error = true
                 if(error.toString().includes("Network Error"))
                 this.errorMsg = "Network Error: can't connect to flask!"
                 else
                 this.errorMsg = "Invalid username or password!"
                 if (error.response.status === 408 || error.code === 'ECONNABORTED')
                 this.errorMsg = "Connection to flask server was aborted due to timeout limit!"
                 this.loading = false
                 return
            })
          
            
        
     
},


  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.main-section {

.container{
  width:32rem;
}

}

</style>
